import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import BibleList from '../components/BibleList';
import Ad from '../components/Ad';
import './styles.css';
import './main.css';
import { Helmet } from 'react-helmet';
import { useSearchParams } from 'react-router-dom';

function Home() {
  const [searchParams, setSearchParams] = useSearchParams();
  const [type, setType] = useState('ja');
  const [oldTitle, setOldTitle] = useState('');
  const [newTitle, setNewTitle] = useState('');

  useEffect(() => {
    let urlType = searchParams.get('type') || 'norm';
    if (!['norm', 'en'].includes(urlType)) {
      urlType = 'norm';
      setSearchParams({ type: 'norm' });
    }

    setType(urlType);

    // Update titles based on type
    setOldTitle(urlType === 'en' ? 'The Old Testament' : '');
    setNewTitle(urlType === 'en' ? 'The New Testament' : '');
  }, [searchParams, setSearchParams]);

  return (
    <div className="body">
      <Header />
      <div className="container">
        <BibleList
          title={oldTitle}
          type={type}
          books={[
            ['genesis', ''],
            ['exodus', ''],
            ['leviticus', ''],
            ['numbers', ''],
            ['deuteronomy', ''],
            ['joshua', ''],
            ['judges', ''],
            ['ruth', ''],
            ['1samuel', ' '],
            ['2samuel', ' '],
            ['1kings', ' '],
            ['2kings', ' '],
            ['1chronicles', ' '],
            ['2chronicles', ' '],
            ['ezra', ''],
            ['nehemiah', ''],
            ['esther', ''],
            ['job', ''],
            ['psalms', ''],
            ['proverbs', ''],
            ['ecclesiastes', ''],
            ['songofsongs', ''],
            ['isaiah', ''],
            ['jeremiah', ''],
            ['lamentations', ''],
            ['ezekiel', ''],
            ['daniel', ''],
            ['hosea', ''],
            ['joel', ''],
            ['amos', ''],
            ['obadiah', ''],
            ['jonah', ''],
            ['micah', ''],
            ['nahum', ''],
            ['habakkuk', ''],
            ['zephaniah', ''],
            ['haggai', ''],
            ['zecariah', ''],
            ['malachi', '']
          ]}
        />
        <br/>
        <BibleList
          title={newTitle}
          type={type}
          books={[
            ['matthew', ''],
            ['mark', ''],
            ['luke', ''],
            ['john', ''],
            ['acts', ''],
            ['romans', ''],
            ['1corinthians', ''],
            ['2corinthians', ''],
            ['galatians', ''],
            ['ephesians', ''],
            ['philippians', ''],
            ['colossians', ''],
            ['1thessalonians', ''],
            ['2thessalonians', ''],
            ['1timothy', ''],
            ['2timothy', ''],
            ['titus', ''],
            ['philemon', ''],
            ['hebrews', ''],
            ['james', ''],
            ['1peter', ''],
            ['2peter', ''],
            ['1john', ''],
            ['2john', ''],
            ['3john', ''],
            ['jude', ''],
            ['revelation', '']
          ]}
        />
        <br/>
        <Ad />
      </div>
      <Footer />
    </div>
  );
}

export default Home;
